import { createContext, useEffect, useState } from "react";
import { Notification } from "./utils";
export const PostContext = createContext();

async function sortData(data) {
    return Promise.resolve(
        data.sort((a, b) => {
            if (a.slug.toLowerCase() < b.slug.toLowerCase()) return -1;
            else if (a.slug.toLowerCase() > b.slug.toLowerCase()) return 1;
            else return 0;
        })
    );
}

function PostsBlock({ children }) {
    const [posts, setPosts] = useState([]);
    const [pages, setPages] = useState([]);
    const [loading, setLoading] = useState(true);

    const issue = {
        issue: false,
        warned: false,
    };

    const pages_url =
        "https://chatcloud.co/wp-json/wp/v2/pages?per_page=30&orderby=slug&order=asc&_embed=true&exclude=10161,10608,8972,9715,10982,10582,10394,10394,10618";

    const posts_url = "https://chatcloud.co/wp-json/wp/v2/posts?_embed";
    useEffect(() => {
        function fetchData() {
            async function getPosts() {
                try {
                    let data = await fetch(posts_url);
                    data = await data.json();
                    return data;
                } catch (err) {
                    throw err.toString();
                }
            }
            async function getPages() {
                try {
                    let data = await fetch(pages_url);
                    data = await data.json();
                    return await sortData(data);
                } catch (err) {
                    throw err.toString();
                }
            }
            Promise.all([getPosts(), getPages()])
                .then((data) => {
                    setPosts(data[0]);
                    setPages(data[1]);
                    issue.issue &&
                        Notification(
                            "success",
                            "Connection Restored",
                            `You're back Online!.`,
                            5000
                        );
                })
                .catch((err) => {
                    issue.issue = true;
                    setLoading(false);
                    if (err === "TypeError: Failed to fetch") {
                        return (
                            issue.issue &&
                            !issue.warned &&
                            Notification(
                                "warning",
                                "Failed to Fecth some Data",
                                `Some data could not be fetched as the server is currently having issus, you can try again later!`,
                                5000
                            )
                        );
                    }
                    issue.issue &&
                        !issue.warned &&
                        Notification(
                            "danger",
                            "No Connection",
                            `You have no Internet Connection or its unstable, some Pages may not render Correctly!`,
                            5000
                        );
                    issue.warned = true;
                    setTimeout(() => {
                        fetchData();
                    }, 1000);
                })
                .then(() => {
                    setLoading(false);
                });
        }
        fetchData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <PostContext.Provider value={{ posts, setPosts, loading, setLoading, pages, setPages }}>
            {children}
        </PostContext.Provider>
    );
}

export default PostsBlock;
